/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';
import { Subject, debounceTime } from 'rxjs';
import { MessageService } from 'primeng/api';
import {
  AddDeliveryCourierInput,
  AddNoteInput,
  CartItems,
  CartService,
  CheckUncheckCartInput,
  CheckUncheckCartItemInput,
  DeleteCartItemInput,
  DeliveryCourier,
  DeliveryService,
  EditCartItemInput,
  LibCurrencyPipe,
  ListDeliveryCourierInput,
  RefreshPriceInput,
  ResponseCheckCartDto,
  ResponseListCartDto,
  Warehouse
} from '@youtap-web-lib';
import { AuthData } from '../../../shared/data/auth.data';
import { CountCartService } from '../../../shared/service/count-cart.service';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { CartData, CartDeliveryCourier } from '../../../shared/data/cart.data';

interface ShowHideNoteInterface {
  id: string;
  show: boolean;
  value: string;
}

interface CourierDropdown {
  name: string;
  code: string;
  cartId?: string;
}

interface InfoCourierInterface {
  info: string;
  feeDelivery: number;
  show: boolean;
}

interface PriceChange {
  id: string;
  cartItemId: string;
  productName: string;
  priceBefore: number;
  priceAfter: number;
  quantity: number;
  image: string;
  variant: string;
  category: string;
}

interface ListCourierInterface {
  cartId: string;
  courier: CourierDropdown[];
}
export interface WarehousePriceChange {
  warehouse: Warehouse;
  priceChange: PriceChange[];
  isPriceChange: boolean;
}
@Component({
  selector: 'list-cart',
  templateUrl: './list-cart.component.html',
  styleUrls: ['./list-cart.component.scss'],
  animations: [
    trigger('toastAnimation', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter, :leave', [animate('0.3s')])
    ])
  ]
})
export class ListCartComponent implements OnInit {
  isLoading = false;
  dataListCart: ResponseListCartDto[] = [];
  dataCheckCart: ResponseCheckCartDto[] = [];
  showModal = false;
  @Input() search = '';
  @Input() showBackButton = false;
  notes: ShowHideNoteInterface[] = [];
  loadingCourier = true;
  listCourier: ListCourierInterface[] = [];
  selectedCourier: CourierDropdown[] = [];
  listDeliveryCourier: DeliveryCourier[][] = [];
  infoCourier: InfoCourierInterface[] = [];
  totalPurchase = 0;
  disabledNext = true;
  loadingNext = false;
  priceChange = {
    show: false,
    product: [] as PriceChange[]
  };
  addNoteNotifier = new Subject();
  getListDeliveryCourierNotifier = new Subject();
  addReduceQtyNotifier = new Subject();
  haveCourier = false;
  modalWarningPod = false;
  showToast = false;
  isPromo = false;
  warehousePriceChange = [] as WarehousePriceChange[];
  isPriceChange = false;

  constructor(
    private router: Router,
    private cartService: CartService,
    private activatedRouted: ActivatedRoute,
    public authData: AuthData,
    private deliveryService: DeliveryService,
    private currencyPipe: LibCurrencyPipe,
    private cartData: CartData,
    private countCartService: CountCartService,
    private route: ActivatedRoute,
    private messageService: MessageService,
    private modalService: ModalService,
    private WindowActionService: WindowActionService
  ) {}

  // eslint-disable-next-line @angular-eslint/use-lifecycle-interface
  ngAfterViewInit(): void {
    this.getListCart();
  }

  ngOnInit(): void {
    this.getListCart();
    if (this.activatedRouted.snapshot?.data['canGoBack']) {
      this.showBackButton =
        this.activatedRouted.snapshot?.data['canGoBack'] ?? true;
    }

    localStorage.removeItem('deliveryCouriers');

    this.addNoteNotifier.pipe(debounceTime(500)).subscribe((notes: any) => {
      this.saveNote(notes.id, notes.value);
    });
    this.addReduceQtyNotifier.pipe(debounceTime(500)).subscribe((cart: any) => {
      this.editCartItem(cart.id, cart.qty, cart.cartId);
    });
    this.getListDeliveryCourierNotifier
      .pipe(debounceTime(500))
      .subscribe((cartId: any) => {
        this.getListCourierCategory(cartId);
      });
  }

  checkPod() {
    const cartPod = this.dataCheckCart.filter(
      (val) =>
        val.isPODAvailable &&
        this.checkCourierPod(val.deliveryCourierId).length > 0
    );
    if (cartPod.length > 1) {
      this.modalWarningPod = true;
    } else {
      this.checkCart();
    }
  }

  checkCourierPod(deliveryCourierId: string) {
    let deliveryCourier = [] as DeliveryCourier[];
    this.listDeliveryCourier.forEach((courier, index) => {
      if (courier.length > 0) {
        deliveryCourier = this.listDeliveryCourier[index].filter(
          (val) => val.id === deliveryCourierId && val.code === 'internal'
        );
      }
    });
    return deliveryCourier;
  }

  checkCart() {
    this.loadingNext = true;
    this.cartService.checkCartQuery().subscribe({
      next: async ({ data }: any) => {
        let disabledNext = true;
        // let showPriceChange = false;
        this.dataCheckCart = data.checkCart;
        this.sumTotalPurchase();
        if (data.checkCart.length > 0) {
          disabledNext = false;
          this.dataCheckCart.forEach((cart) => {
            if (cart.error?.message) {
              disabledNext = true;
            }
            const warehousePriceChangeIndex =
            this.warehousePriceChange.findIndex(
              (val) => val.warehouse.id === cart.warehouse.id
            );
          const warehousePriceChangeVal =
            this.warehousePriceChange[warehousePriceChangeIndex];

          if (cart.error?.message) {
            disabledNext = true;
          }

            if (!disabledNext) {
              cart.cartItems.forEach((item) => {
                if (item.errors.length > 0) {
                  if (
                    item.isSelected &&
                    item.errors[0].code !== 'PRICE_HAS_CHANGED'
                  ) {
                    disabledNext = true;
                  }
                  const priceChangeIndex =
                    warehousePriceChangeVal.priceChange.findIndex(
                      (priceChange) =>
                        priceChange.id ===
                        item.productVariantWarehouse.productVariant.id
                    );
                  warehousePriceChangeVal.priceChange[
                    priceChangeIndex
                  ].priceAfter = item.productVariantWarehouse.price;
                  if (
                    warehousePriceChangeVal.priceChange[priceChangeIndex]
                      .priceBefore !== item.productVariantWarehouse.price
                  ) {
                    this.isPriceChange = true;
                    this.warehousePriceChange[warehousePriceChangeIndex].isPriceChange =
                      true;
                  }
                }
              });
            }

            const cartIndex = this.dataListCart.findIndex(
              (listCart) => listCart.id === cart.id
            );
            if (cart.error) {
              this.dataListCart[cartIndex].error = cart.error;
            }
            this.dataListCart[cartIndex].cartItems = cart.cartItems;
          });
          const checkCourier = this.selectedCourier.findIndex(
            (courier) => courier.code === ''
          );
          if (checkCourier > -1) {
            disabledNext = true;
          }
          this.loadingNext = false;
          this.disabledNext = disabledNext;
          this.priceChange.show = this.isPriceChange;
          if (!this.isPriceChange) {
            this.goToDetailPayment();
          }
        } else {
          this.disabledNext = false;
          this.messageService.add({
            key: 'bc',
            severity: 'error',
            detail: 'Tidak ada barang yang di checkout'
          });
        }
      },
      error: () => {
        this.loadingNext = false;
      }
    });
  }

  async goToDetailPaymentWithRefreshPrice() {
    const refreshPricePromises: any[] = [];
    this.priceChange.product.forEach((price) => {
      if (price.priceBefore !== price.priceAfter) {
        refreshPricePromises.push(this.refreshPrice(price.cartItemId));
      }
    });
    await Promise.all(refreshPricePromises);
    setTimeout(() => {
      this.goToDetailPayment();
    }, 1000);
  }

  refreshPrice(id: string) {
    const data: RefreshPriceInput = {
      refreshPriceInput: {
        id
      }
    };

    this.cartService.refreshPrice(data).subscribe({
      next: () => {
        return true;
      }
    });
  }

  goToDetailPayment() {
    this.router.navigate(['/cart/payment'], {
      queryParams: {
        type: 'list'
      }
    });
  }

  saveNote(id: string, note: string) {
    const data: AddNoteInput = {
      addNoteInput: {
        id,
        note
      }
    };
    this.cartService.addNote(data).subscribe({
      next: () => {
        return true;
      }
    });
  }

  goToHome() {
    this.router.navigate(['/']);
  }

  getListCart(cartId?: string) {
    this.isLoading = true;
    this.cartService.listCartQuery().subscribe({
      next: ({ data }: any) => {
        if (data) {
          if (cartId) {
            const cartIndex = this.dataListCart.findIndex(
              (cart) => cart.id === cartId
            );
            const cartIndex2 = data.listCart.findIndex(
              (cart: any) => cart.id === cartId
            );
            if (cartIndex2 >= 0) {
              this.dataListCart[cartIndex] = data.listCart[cartIndex2];
              this.dataListCart = data.listCart;
            } else {
              this.dataListCart.splice(cartIndex, 1);
            }
          } else {
            this.dataListCart = data.listCart;
          }
          this.isLoading = false;
          if (this.notes.length === 0) {
            data.listCart.forEach((cart: any) => {
              this.notes.push({
                id: cart.id,
                show: false,
                value: cart.note
              });
              this.infoCourier.push({
                info: '',
                show: false,
                feeDelivery: 0
              });
              this.listCourier.push({
                cartId: cart.id,
                courier: []
              });
              this.getListCourierCategory(cart.id);
            });
          }
          this.getCheckCart();
        }
      },
      error: () => {
        this.isLoading = false;
      }
    });
  }

  getCheckCart() {
    this.disabledNext = true;
    this.priceChange.product = [] as PriceChange[];
    this.isLoading = true;

    this.cartService.checkCartQuery().subscribe({
      next: ({ data }: any) => {
        let disabledNext = true;
        this.dataCheckCart = data.checkCart;

        if (data.checkCart.length === 0 && this.dataListCart.length !== 0) {
          this.showToast = true;
          setTimeout(() => {
            this.showToast = false;
          }, 3000);
        }
        this.sumTotalPurchase();
        if (data.checkCart.length > 0) {
          disabledNext = false;
          const priceChange = [] as PriceChange[];

          this.dataCheckCart.forEach((cart) => {
            if (cart.error?.message) {
              disabledNext = true;
            }
            cart.cartItems.forEach((item) => {
              priceChange.push({
                id: item.productVariantWarehouse.productVariant.id,
                cartItemId: item.id,
                productName:
                  item.productVariantWarehouse.productVariant.product.name,
                priceBefore: item.productVariantWarehouse.price,
                priceAfter: item.productVariantWarehouse.price,
                quantity: item.quantity,
                image:
                  item.productVariantWarehouse.productVariant.product.image,
                variant: item.productVariantWarehouse.productVariant.name,
                category:
                  item.productVariantWarehouse.productVariant.product
                    .categoryProducts.length > 0
                    ? item.productVariantWarehouse.productVariant.product
                        .categoryProducts[0].category.name
                    : ''
                
              });
              if (
                item.errors.length > 0 &&
                item.isSelected &&
                item.errors[0].code !== 'PRICE_HAS_CHANGED'
              ) {
                disabledNext = true;
              }
            });
            const cartIndex = this.dataListCart.findIndex(
              (listCart) => listCart.id === cart.id
            );
            if (cart.error) {
              this.dataListCart[cartIndex].error = cart.error;
            }
            this.warehousePriceChange.push({
              warehouse: cart.warehouse,
              priceChange: priceChange,
              isPriceChange: false,
            });
          });
          const checkCourier = this.selectedCourier.findIndex(
            (courier) => courier.code === ''
          );
          if (checkCourier > -1) {
            disabledNext = true;
          }
          this.disabledNext = disabledNext;
        }
        this.checkHaveCourier();
        this.isLoading = false;
      },
      error: () => {
        this.isLoading = false;
      }
    });
  }

  sumTotalPurchase() {
    let totalCart = 0;
    if (this.dataCheckCart.length > 0) {
      totalCart = this.dataCheckCart.reduce(
        (sum: number, cart: ResponseCheckCartDto) => (sum += cart.total),
        0
      );
    }
    const deliveryCouriers = this.cartData.deliveryCouriers;
    let totalFeeDelivery = 0;
    if (deliveryCouriers && deliveryCouriers.length > 0) {
      totalFeeDelivery = deliveryCouriers.reduce(
        (sum: number, delivery: CartDeliveryCourier) =>
          (sum += delivery.deliveryCourier?.price || 0),
        0
      );
    }
    this.totalPurchase = totalCart + totalFeeDelivery;
  }

  checkUncheckCart(id: string, isSelected: boolean) {
    this.loadingCourier = true;
    const data: CheckUncheckCartInput = {
      checkUncheckCartInput: {
        id,
        isSelected
      }
    };
    this.cartService.checkUncheckCart(data).subscribe({
      next: () => {
        this.getListCart(id);
        this.getListDeliveryCourierNotifier.next(id);
      }
    });
  }

  checkUncheckCartItem(id: string, cartId: string) {
    this.loadingCourier = true;
    const data: CheckUncheckCartItemInput = {
      id
    };
    this.cartService.checkUncheckCartItem(data).subscribe({
      next: () => {
        this.getListCart(cartId);
        this.getListDeliveryCourierNotifier.next(cartId);
      }
    });
  }

  checkedSupplier(cartItems: CartItems[]) {
    const selected = cartItems.filter((item) => !item.isSelected);
    if (selected.length > 0) {
      return false;
    } else {
      return true;
    }
  }

  addReduceQty(id: string, $qty: number, cartId: string) {
    const data = {
      id,
      qty: $qty,
      cartId
    };
    this.addReduceQtyNotifier.next(data);
  }

  editCartItem(id: string, quantity: number, cartId: string) {
    this.loadingCourier = true;
    const data: EditCartItemInput = {
      editCartInput: {
        id,
        quantity
      }
    };
    this.cartService.editCartItem(data).subscribe({
      next: () => {
        this.loadingCourier = false;
        this.disabledNext = false;
        this.getListCart(cartId);
        this.getListDeliveryCourierNotifier.next(cartId);
        this.countCartService.getCountCart();
      },
      error: () => {
        this.loadingCourier = false;
        this.disabledNext = true;
      }
    });
  }

  deleteCartItem(id: string, cartId: string) {
    this.loadingCourier = true;
    const data: DeleteCartItemInput = {
      id
    };
    this.cartService.deleteCartItem(data).subscribe({
      next: () => {
        this.notes = [];
        this.infoCourier = [];
        this.listCourier = [];
        this.getListCart(cartId);
        this.countCartService.getCountCart();
      }
    });
  }

  getListCourierCategory(cartId: string) {
    const data: ListDeliveryCourierInput = {
      listDeliveryCourierInput: {
        page: 0,
        limit: 25,
        cartId
      }
    };
    const listCourierIndex = this.listCourier.findIndex(
      (listCourier) => listCourier.cartId === cartId
    );
    this.listCourier[listCourierIndex].courier = [] as CourierDropdown[];
    this.selectedCourier[listCourierIndex] = {} as CourierDropdown;
    this.infoCourier[listCourierIndex].show = false;
    this.deliveryService.listDeliveryCourier(data).subscribe({
      next: ({ data }) => {
        data.listDeliveryCourier.deliveryCouriers.forEach((courier) => {
          this.listCourier[listCourierIndex].courier.push({
            cartId: cartId,
            name: courier.name,
            code: courier.id
          });
        });
        // this.listDeliveryCourier = data.listDeliveryCourier.deliveryCouriers;
        this.listDeliveryCourier[listCourierIndex] =
          data.listDeliveryCourier.deliveryCouriers;
        this.checkHaveCourier();
        const courier = data.listDeliveryCourier.deliveryCouriers.filter(
          (val) => val.code === 'internal'
        );
        this.checkIsPod(cartId, courier[0], listCourierIndex);
        this.loadingCourier = false;
      },
      error: () => {
        this.cartData.deliveryCouriers = [
          {
            cartId,
            deliveryCourier: {} as DeliveryCourier
          }
        ];
        this.loadingCourier = false;
        this.sumTotalPurchase();
      }
    });
  }

  selectCourier($event: any, cartId: string, index: number) {
    const courierCategoryId = $event.value.code;
    if (courierCategoryId !== '') {
      const deliveryCourier = this.listDeliveryCourier[index].find(
        (deliveryCourier) => deliveryCourier.id === courierCategoryId
      );

      if (deliveryCourier) {
        this.infoCourier[index].show = true;
        const code = deliveryCourier.code;
        const displayName = deliveryCourier.provider.displayName;
        const name = deliveryCourier.name;
        const price = this.currencyPipe.transform(deliveryCourier.price, false);
        const etd = deliveryCourier.estimatedTimeDelivery;
        this.infoCourier[index].info = `${displayName} - ${name}(${price})`;
        if (code !== 'internal') {
          this.infoCourier[index].info += `<br>Estimasi ${etd}`;
        }
        this.infoCourier[index].feeDelivery = deliveryCourier.price;
        this.cartData.deliveryCouriers = [
          {
            cartId,
            deliveryCourier: deliveryCourier
          }
        ];
        this.sumTotalPurchase();
        this.addDeliveryCourier(cartId, deliveryCourier.id);
        this.checkHaveCourier();
      }
    } else {
      this.infoCourier[index].show = false;
      this.cartData.deliveryCouriers = [
        {
          cartId,
          deliveryCourier: {} as DeliveryCourier
        }
      ];
      this.getCheckCart();
    }
  }

  checkHaveCourier() {
    const cartSelected = this.dataListCart.filter(
      (val) => val.cartItems.filter((item) => item.isSelected).length
    );
    const findHaveCourier = cartSelected.filter(
      (cart) =>
        !this.selectedCourier.filter((courier) => courier.cartId === cart.id)
          .length
    );
    if (findHaveCourier.length) {
      this.haveCourier = false;
    } else {
      this.haveCourier = true;
    }
  }

  checkIsPod(
    cartId: string,
    courier: DeliveryCourier,
    listCourierIndex: number
  ) {
    const cartIndex = this.dataListCart.findIndex((val) => val.id === cartId);
    const dataCart = this.dataListCart.filter((val) => val.id === cartId);
    if (dataCart[0].isPODAvailable) {
      const $event = {
        value: {
          code: courier.id
        }
      };
      this.selectCourier($event, cartId, cartIndex);
      this.selectedCourier[listCourierIndex] = {
        cartId,
        code: courier.id,
        name: courier.name
      } as CourierDropdown;
    }
  }

  addDeliveryCourier(id: string, deliveryCourierId: string) {
    const data: AddDeliveryCourierInput = {
      addDeliveryCourierInput: {
        id,
        deliveryCourierId
      }
    };
    this.deliveryService.addDeliveryCourier(data).subscribe({
      next: () => {
        this.getListCart(id);
      }
    });
  }

  addNote($value: any, index: number) {
    this.notes[index].value = $value;
    this.addNoteNotifier.next(this.notes[index]);
  }

  getErrorMessageCart(cartId: string) {
    if (this.dataCheckCart.length > 0) {
      const cart = this.dataCheckCart.find(
        (checkCart) => checkCart.id === cartId
      );
      return cart?.error;
    } else {
      return null;
    }
  }

  goToDetailSupplier(slug?: string) {
    this.router.navigate(['supplier/detail/'], {
      queryParams: {
        s: slug
      }
    });
  }
}
