/* eslint-disable @typescript-eslint/no-explicit-any */
import {AfterViewInit, Component, OnInit} from '@angular/core';
import {
  Address,
  CartService,
  PriceAdjustment,
  ResponseListCartDto,
  VoucherService,
  Voucher,
  ListEligibleVoucherResponse
} from '@youtap-web-lib';
import {AuthData} from '../../../shared/data/auth.data';
import {CartData, CartDeliveryCourier} from '../../../shared/data/cart.data';
import {
  PaymentData,
  PaymentDataType
} from '../../../shared/data/payment.data';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import {GaService} from '../../../shared/service/ga.service';
import {ModalService} from '../../../shared/service/modal/modal.service';
import {EligibleVoucherComponent} from './eligible-voucher/eligible-voucher.component';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'payment-summary',
  templateUrl: './payment-summary.component.html'
})
export class PaymentSummaryComponent implements OnInit, AfterViewInit {
  loadingVoucher = false;
  isLoading = true;
  dataCheckCart: ResponseListCartDto[] = [];
  shoppingSummary = {
    totalShopping: 0,
    deliveryFee: 0,
    otherFee: 0,
    total: 0
  };
  address!: Address;
  showDetails: boolean[] = [];
  voucherValue = 0;
  couponCode = '';
  couponCodeText = '';
  errorMessage = '';
  confirmDelete = false;
  checkedTnc = false;
  showTnCModal = false;
  outOfStock = {
    loading: false,
    modal: false
  };
  isFalsePodAvailable = '';
  pageForm = false;
  totalEligibleVouchers!: number | undefined;
  voucher!: Voucher;
  planOptionIds: string[] = [];
  listVoucher!: ListEligibleVoucherResponse;
  isShowModal = false;

  constructor(
    private authData: AuthData,
    private cartService: CartService,
    private voucherService: VoucherService,
    private cartData: CartData,
    private paymentData: PaymentData,
    private router: Router,
    private location: Location,
    private activatedRoute: ActivatedRoute,
    private gaService: GaService,
    private modalService: ModalService,
    private messageService: MessageService
  ) {
  }

  ngOnInit(): void {
    this.getListVoucher();
    this.address = this.authData.location;
    // programatically check condition incomarco if route from cart
    if (this.activatedRoute.snapshot?.queryParamMap.get('type') !== 'list') {
      this.pageForm = true;
    } else {
      this.location.replaceState('/cart/payment');
    }
    this.couponCodeText = history.state ? history.state['couponCodeText'] : '';
    this.voucherValue = history.state ? history.state['voucherValue'] : '';
  }

  ngAfterViewInit(): void {
    this.getCheckCart();
  }

  getCheckCart() {
    this.isLoading = true;
    this.cartService.checkCartQuery().subscribe({
      next: ({data}: any) => {
        this.isLoading = false;
        if (data) {
          this.dataCheckCart = data.checkCart;
          if(this.dataCheckCart.length > 0) {
            if (this.pageForm) {
              this.checkOutOfStock(data.checkCart);
            } else {
              this.filterCartItem();
            }
            this.checkPODAvailable();
            this.setSummeryPayment();
            this.calculateTotalBySupplier();
            this.addCourierName();
          }
          else {
            this.messageService.add({
              key: 'bc',
              severity: 'error',
              detail: 'Tidak ada barang yang di checkout'
            })
            setTimeout(() => {
              this.location.back();
            }, 3000)
          }
        }
      },
      error: () => {
        this.isLoading = false;
      }
    });
  }

  addCourierName() {
    this.dataCheckCart.forEach((cart, i) => {
      const index = this.cartData.deliveryCouriers.findIndex((val) => val.cartId == cart.id)
      this.dataCheckCart[i].deliveryCourierCode =
        this.cartData.deliveryCouriers[index].deliveryCourier.code;
    });
  }

  filterCartItem() {
    this.dataCheckCart.forEach((val, index) => {
      this.dataCheckCart[index].cartItems = val.cartItems.filter(
        (item) => item.isSelected
      );
    });
  }

  checkPODAvailable() {
    const checkFalsePodLength = this.dataCheckCart.filter(
      (value) => value.isPODAvailable === false
    ).length;
    if (checkFalsePodLength > 0) {
      this.isFalsePodAvailable = 'not-available';
    } else {
      this.isFalsePodAvailable = 'available';
    }
  }

  calculateTotalBySupplier() {
    // calculate total by supplier
    this.dataCheckCart.forEach((dataCart, index) => {
      const totalPriceAdjustment = dataCart.priceAdjustments.reduce(
        (sum, price) =>
          sum + this.calculatePriceAdjustment(price, dataCart.total),
        0
      );
      this.shoppingSummary.otherFee += totalPriceAdjustment;
      this.dataCheckCart[index].allTotal =
        (dataCart.allTotal ? dataCart.allTotal : dataCart.total) +
        totalPriceAdjustment;
      let totalQty = 0;
      dataCart.cartItems.forEach((item) => {
        totalQty += item.isSelected ? item.quantity : 0;
      });
      this.dataCheckCart[index].totalQty = totalQty;
    });
  }

  setSummeryPayment() {
    this.dataCheckCart.forEach((cart) => {
      this.showDetails.push(false);
      this.shoppingSummary.total += cart.total;
      cart.cartItems.forEach((item) => {
        if (item.isSelected) {
          this.shoppingSummary.totalShopping =
            this.shoppingSummary.totalShopping + item.subtotal;
        }
      });
      this.getDeliveryCourier();
    });
  }

  getDeliveryCourier() {
    this.shoppingSummary.deliveryFee = this.cartData.deliveryCouriers.reduce(
      (total: number, delivery: CartDeliveryCourier) =>
        (total += delivery.deliveryCourier.price
          ? delivery.deliveryCourier.price
          : 0),
      0
    );
  }


  cancelOrder() {
    this.location.back();
  }

  handleCheckout() {
    this.cartService.checkCartQuery().subscribe({
      next: ({data}: any) => {
        this.isLoading = false;
        if (data) {
          this.dataCheckCart = data.checkCart;
          if(this.dataCheckCart.length > 0) {
            this.gaService.addEvent('Payment', 'checkout', '');
            this.router
            .navigate(['/payment/list'], {
              queryParams: {
                pod: this.isFalsePodAvailable
              }
            })
            .then(() => {
              const totalSummary = this.shoppingSummary.totalShopping + this.shoppingSummary.deliveryFee + this.shoppingSummary.otherFee
              this.paymentData.payment = {
                total: this.voucherValue ? (totalSummary - this.voucherValue) : totalSummary,
                type: PaymentDataType.BTOK,
                voucher: this.couponCodeText
              };
            });
          }
          else {
            this.messageService.add({
              key: 'bc',
              severity: 'error',
              detail: 'Tidak ada barang yang di checkout'
            })
            setTimeout(() => {
              this.location.back();
            }, 3000)
          }
        }
      },
      error: () => {
        this.isLoading = false;
      }
    });
  }

  checkOutOfStock(data: ResponseListCartDto[]) {
    let isOutOfStock = false;
    data.forEach((data) => {
      data.cartItems.forEach((item) => {
        if (
          item.errors.length > 0 &&
          (item.errors[0].code === 'OUT_OF_STOCK' ||
            item.errors[0].code === 'INSUFFICIENT_STOCK')
        ) {
          isOutOfStock = true;
          this.outOfStock.loading = true;
          this.gaService.addEvent(
            item.errors[0].code +
            ' ' +
            item.productVariantWarehouse.productVariant.product.name,
            'payment summary',
            ''
          );
        }
      });
    });
    if (isOutOfStock) {
      setTimeout(() => {
        this.outOfStock.modal = true;
      }, 3000);
    }
  }

  calculatePriceAdjustment(priceAdjustment: PriceAdjustment, subtotal: number) {
    let total = 0;
    if (priceAdjustment.type === 'PERCENTAGE') {
      total = (priceAdjustment.value / 100) * subtotal;
    } else {
      total = priceAdjustment.value;
    }
    return total;
  }


  getListVoucher() {
    this.voucherService.listEligibleVoucher().subscribe({
      next: ({data}) => {
        this.loadingVoucher = false;
        this.listVoucher = data;
        this.totalEligibleVouchers =
          data.listEligibleVoucher?.availableVouchers?.length;
      },
      error: (err) => {
        this.loadingVoucher = false;
        return err;
      }
    });
  }

  showModalVoucher() {
    this.isShowModal = false;
    this.gaService.addEvent('Checkout_button_gunakan_promo', 'checkout', '');
    const modalRef = this.modalService.open(EligibleVoucherComponent, {
      title: '',
      closeButton: false,
      data: {
        isFromSubscription: false,
        listVoucher: this.listVoucher
      }
    });
    modalRef.onResult().subscribe((value) => {
      if (value) {
        this.voucherValue = value.voucherValue;
        this.couponCodeText = value.couponCodeText;
      } else {
        this.voucherValue = 0
        this.couponCodeText = ''
      }
    });
  }
}
