import { Component, Input } from '@angular/core';
import { ResponseListCartDto } from '@youtap-web-lib';

@Component({
  selector: 'out-of-stock-cart',
  templateUrl: './out-of-stock-cart.component.html'
})
export class OutOfStockCartComponent {
  @Input() dataCheckCart: ResponseListCartDto[] = [];

  checkOutOfStock(data: ResponseListCartDto) {
    let isOutOfStock = false;
    data.cartItems.forEach((item) => {
      if(item.errors.length > 0 && (item.errors[0].code === 'OUT_OF_STOCK' || item.errors[0].code === 'INSUFFICIENT_STOCK')) {
        isOutOfStock = true;
      }
    })
    return isOutOfStock;
  }
}
