/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component } from '@angular/core';
import { Router } from '@angular/router';
import {
  Voucher,
  VoucherService,
  CheckEligibilityVoucherDto,
  SCMCheckEligibilityVoucherResponse,
  ResponseListPurchasePackageDto,
  ListEligibleVoucherResponse,
  CheckEligibleVoucherDto,
  BOSCheckEligibleVoucherResponse
} from '@youtap-web-lib';
import { GaService } from 'apps/youtap-bos/src/app/shared/service/ga.service';
import { Modal } from 'apps/youtap-bos/src/app/shared/service/modal/modal.model';
import { InputModalDto } from 'apps/youtap-bos/src/app/shared/service/modal/modal.service';

@Component({
  selector: 'eligible-voucher',
  templateUrl: './eligible-voucher.component.html',

  styleUrls: ['./eligible-voucher.component.scss']
})
export class EligibleVoucherComponent extends Modal {
  listVoucher!: ListEligibleVoucherResponse;
  loading = false;
  selectedIndex!: null | number;
  selectedUneligibleIndex!: null | number;
  loadingVoucher = false;
  showVoucherModal = false;
  errorCouponCode = false;
  couponCode = '';
  couponCodeText = '';
  showCouponCode = false;
  errorMessage = '';
  voucherValue = 0;
  voucherBtok!: SCMCheckEligibilityVoucherResponse;
  voucherSubs!: BOSCheckEligibleVoucherResponse;

  listLoading = false;
  listPurchasePackage!: ResponseListPurchasePackageDto;
  isFromSubscription = false;
  planOptionIds: string[] = [];
  constructor(private router: Router, private voucherService: VoucherService, private gaService: GaService) {
    super();
  }
  selectedVoucher!: Voucher;
  isSelected = false;

  title!: string;
  showModal = false;

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showModal = inputs.closeButton;
    this.listVoucher = inputs.data.listVoucher;
    this.isFromSubscription = inputs.data.isFromSubscription;
    this.planOptionIds = inputs.data.planOptionIds;
  }

  select(value?: any): void {
    this.close(value);
  }

  cancel(): void {
    this.dismiss({});
  }

  checkEligibleVoucher() {
    this.loadingVoucher = true;
    this.gaService.addEvent('Checkout_gunakan_promo_detail', 'checkout', '');
    this.errorMessage = '';
    if (!this.isFromSubscription) {
      const data: CheckEligibilityVoucherDto = {
        checkEligibilityVoucherInput: {
          couponCode: this.couponCode
        }
      };

      this.voucherService.checkEligibilityVoucher(data).subscribe({
        next: ({ data }) => {
          this.gaService.addEvent('Checkout_use_voucher', 'checkout', '');
          this.loadingVoucher = false;
          this.showCouponCode = true;
          this.voucherBtok = data.checkEligibilityVoucher;
          this.voucherValue = this.voucherBtok?.voucherValue;
          this.couponCodeText = this.voucherBtok?.couponCode;
          if (this.voucherValue && this.isSelected) {
            this.select({
              couponCodeText: this.couponCodeText,
              voucherValue: this.voucherValue
            });
          }
        },
        error: (error) => {
          this.loadingVoucher = false;
          this.showCouponCode = false;
          const [graphqlError] = error.graphQLErrors;
          this.errorMessage = graphqlError?.message;
          this.voucherValue = 0;
          this.couponCodeText = '';
          this.errorCouponCode = true;
        }
      });
    }
    if (this.isFromSubscription) {
      const data: CheckEligibleVoucherDto = {
        checkEligibleVoucherInput: {
          couponCode: this.couponCode,
          planOptionIds: this.planOptionIds
        }
      };

      this.voucherService.checkEligibleVoucher(data).subscribe({
        next: ({ data }) => {
          this.loadingVoucher = false;

          this.showCouponCode = true;
          this.voucherValue = data.checkEligibleVoucher?.voucherValue;
          this.couponCodeText = data.checkEligibleVoucher?.couponCode;
          if (this.voucherValue && this.isSelected) {
            this.select({
              couponCodeText: this.couponCodeText,
              voucherValue: this.voucherValue
            });
          }
        },
        error: (error) => {
          this.loadingVoucher = false;
          this.showCouponCode = false;
          const [graphqlError] = error.graphQLErrors;
          this.errorMessage = graphqlError?.message;
          this.voucherValue = 0;
          this.couponCodeText = '';
          this.errorCouponCode = true;
        }
      });
    }
  }

  closeModalVoucher() {
    this.showVoucherModal = false;
    this.couponCode = '';
    this.errorMessage = '';
    this.errorCouponCode = false;
    this.isSelected = false;
    this.selectedIndex = null;
  }

  handleClick(voucher: Voucher, i: number) {
    this.gaService.addEvent('Checkout_choose_voucher', 'checkout', '');
    this.isSelected = true;
    this.errorMessage = '';
    this.selectedIndex = i;
    this.selectedVoucher = voucher;
    this.couponCode = voucher.couponCode;
  }

  goToCart() {
    this.checkEligibleVoucher();
    if (!this.isSelected && this.voucherValue) {
      if (!this.isFromSubscription) {
        this.select({
          couponCodeText: this.couponCodeText,
          voucherValue: this.voucherValue
        });
      }
      if (this.isFromSubscription) {
        this.select({
          couponCodeText: this.couponCodeText,
          voucherValue: this.voucherValue
        });
      }
    }
  }
  handleShowModal() {
    this.showVoucherModal = true;
    this.couponCode = '';
    this.gaService.addEvent('Checkout_input_voucher_code', 'checkout', '');
  }
}
