import { Component, OnInit } from '@angular/core';
import { AuthData } from '../../../shared/data/auth.data';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { ListAddressModalComponent } from '../../home/list-address-modal/list-address-modal.component';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { Router } from '@angular/router';
import { HeaderModalComponent } from '../../home/header-modal/header-modal.component';

@Component({
  selector: 'address-cart',
  templateUrl: './address-cart.component.html'
})
export class AddressCartComponent implements OnInit {
  address = {
    show: false,
    name: '',
    recipientName: '',
    recipientPhone: '',
    detail: ''
  };

  constructor(
    public authData: AuthData,
    private modalService: ModalService,
    private windowActionService: WindowActionService,
    private router: Router
  ) {}

  ngOnInit(): void {
    if (Object.keys(this.authData.location).length) {
      const location = this.authData.location;
      this.address = {
        show: true,
        name: location.name,
        recipientName: location.recipientName,
        recipientPhone: location.recipientPhone,
        detail: `${location.addressDetail}, ${location.regency.name}, ${location.province.name}, ${location.postalCode}`
      };
    }
  }

  setLocation() {
    this.router.navigate(["/address/list"]);
  }

  listAddressModal() {
    const modalRef = this.modalService.open(ListAddressModalComponent);
    modalRef.onResult().subscribe((button) => {
      switch (button) {
        case 'use':
          this.windowActionService.reload();
          break;
        case 'add':
          this.router.navigate(['/address/add'], {});
          break;
        default:
          break;
      }
    });
  }

  headerModal() {
    const modalRef = this.modalService.open(HeaderModalComponent, {
      title: 'Kirim belanja stok kemana?',
      closeButton: true
    });
    modalRef.onResult().subscribe((closed) => {
      if (closed === 'location') {
        this.router.navigate(['/location/set'], {});
      } else if (closed === 'login') {
        this.router.navigate(['/login']);
      }
    });
  }
}
