import { isPlatformBrowser } from "@angular/common";
import { Inject, Injectable, PLATFORM_ID } from "@angular/core";
import {DeliveryCourier} from "@youtap-web-lib";

export interface CartDeliveryCourier {
  cartId: string,
  deliveryCourier: DeliveryCourier
}

@Injectable({
  providedIn: "root"
})
export class CartData {
  isReady = false;

  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  /** DELIVERY COURIER */
  set deliveryCouriers(data: CartDeliveryCourier[]) {
    if (this.isReady) {
      const local = localStorage.getItem("deliveryCouriers");
      if(local) {
        const deliveryCouriers: CartDeliveryCourier[] = JSON.parse(local) as CartDeliveryCourier[];
        const deliveryCourierIndex = deliveryCouriers.findIndex((deliveryCourier) => deliveryCourier.cartId === data[0].cartId);
        if(deliveryCourierIndex > -1) {
          deliveryCouriers.splice(deliveryCourierIndex, 1);
        }
        deliveryCouriers.push(data[0]);
        localStorage.setItem("deliveryCouriers", JSON.stringify(deliveryCouriers));
      }
      else {
        const deliveryCouriers: CartDeliveryCourier[] = [data[0]] as CartDeliveryCourier[];
        localStorage.setItem("deliveryCouriers", JSON.stringify(deliveryCouriers));
      }
    }
  }

  get deliveryCouriers(): CartDeliveryCourier[] {
    if(this.isReady) {
      const local = localStorage.getItem("deliveryCouriers");
      if(local) {
        const deliveryCouriers: CartDeliveryCourier[] = JSON.parse(local) as CartDeliveryCourier[];
        return deliveryCouriers;
      } else {
        return [] as CartDeliveryCourier[];
      }
    } else {
      return [] as CartDeliveryCourier[];
    }
  }
}
