import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListCartComponent } from './list-cart/list-cart.component';
import { CartContainerComponent } from '../../shared/layout/container/cart-container/cart-container.component';
import { PaymentSummaryComponent } from './payment-summary/payment-summary.component';
import { EligibleVoucherComponent } from './payment-summary/eligible-voucher/eligible-voucher.component';

const routes: Routes = [
  {
    path: 'list',
    component: CartContainerComponent,
    children: [
      {
        path: '',
        component: ListCartComponent,
        title: 'Keranjang-Kamu',
        data: {
          pageTitle: 'Keranjang Kamu'
        }
      }
    ]
  },
  {
    path: 'payment',
    component: CartContainerComponent,
    children: [
      {
        path: '',
        component: PaymentSummaryComponent,
        title: 'Pembayaran'
      }
    ]
  },
  {
    path: 'eligible-voucher',
    children: [
      {
        path: '',
        component: EligibleVoucherComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CartRoutingModule {}
